import React, { Component } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faEdit } from "@fortawesome/free-solid-svg-icons";
import Notes from "./Notes";
export default class CreateNotes extends Component {
  state = {
    title: "",
    note: "",
    data: [],
    isEdit: false,
    id: null,
  };

  textChangeHandle = (event) => {
    this.setState({ [event.target.name]: event.target.value });
  };
  addNotesHandler = (event) => {
    event.preventDefault();
    const note = {
      title: this.state.title,
      note: this.state.note,
    };
    const data = [...this.state.data];
    data.push(note);
    this.setState({ title: "", note: "", data: data });
    this.refs.title.focus();
  };
  removeNotesHandler = (index) => {
    this.state.data.splice(index, 1);
    this.setState({
      title: "",
      note: "",
      data: this.state.data,
      isEdit: false,
    });
    this.refs.title.focus();
  };
  editNoteHandler = (index) => {
    this.refs.title.focus();
    const note = { ...this.state.data[index] };
    // console.log(note);
    this.setState({
      title: note.title,
      note: note.note,
      isEdit: true,
      id: index,
    });
  };
  updateNoteHandler = (event) => {
    event.preventDefault();
    const title = this.state.title;
    const note = this.state.note;
    const data = [...this.state.data];
    data[this.state.id].title = title;
    data[this.state.id].note = note;
    this.setState({ title: "", note: "", data: data, isEdit: false, id: null });
    this.refs.title.focus();
  };
  componentDidMount = () => {
    this.refs.title.focus();
  };
  render() {
    return (
      <>
        <div className="create-note-component">
          <form
            onSubmit={
              this.state.isEdit ? this.updateNoteHandler : this.addNotesHandler
            }>
            <input
              ref="title"
              name="title"
              type="text"
              placeholder="Title Here..."
              value={this.state.title}
              autoComplete="off"
              onChange={this.textChangeHandle}
            />
            <textarea
              name="note"
              rows={10}
              value={this.state.note}
              placeholder="Write a Note...."
              onChange={this.textChangeHandle}
            />
            <button
              className="addBtn"
              disabled={this.state.title === "" || this.state.note === ""}>
              <FontAwesomeIcon icon={this.state.isEdit ? faEdit : faPlus} />
            </button>
          </form>
        </div>
        <div className="note_container">
          {this.state.data.map((cur, index) => {
            return (
              <Notes
                key={index}
                data={cur}
                index={index}
                removeNotesHandler={this.removeNotesHandler}
                editNotesHandler={this.editNoteHandler}
              />
            );
          })}
        </div>
      </>
    );
  }
}
