import React from "react";
import "./App.css";
import { faTrashAlt, faEdit } from "@fortawesome/free-solid-svg-icons";
import Button from "./MiniButton";

const Note = (props) => {
  return (
    <div className="notes">
      <h1>{props.data.title}</h1>
      <p>{props.data.note}</p>
      <Button
        icon={faTrashAlt}
        delete={() => props.removeNotesHandler(props.index)}
        classes="deleteBtn"
      />
      <Button
        icon={faEdit}
        delete={() => props.editNotesHandler(props.index)}
        classes="editBtn"
      />
    </div>
  );
};

export default Note;
